"use client";

import React, { useState } from "react";
import Sidebar from "@/components/Sidebar";
import { Input } from "@/components/ui/input";
import { ShieldCheck, CheckCircle2, Circle, AlertCircle } from "lucide-react";

export default function ResumeBuilder() {
  // Ê®°ÊãüÂÆ°ËÆ°Áä∂ÊÄÅÔºöÊ†πÊçÆËæìÂÖ•ÂÜÖÂÆπËá™Âä®ÂàáÊç¢
  const [auditStatus, setAuditStatus] = useState({
    email: true,
    location: true,
    impacts: false, // ÈªòËÆ§Êú™ÂÆåÊàêÔºåÈúÄË¶Å AI Ê∂¶Ëâ≤
    verbs: true,
  });

  return (
    <div className="pl-64 flex bg-slate-50 min-h-screen">
      <Sidebar />
      <main className="flex-1 p-12 flex gap-10">
        
        {/* Â∑¶‰æßÊµÅÁ®ãÂØºËà™ (Identity -> Impact -> Academia...) */}
        <div className="w-24 flex flex-col gap-6 pt-10">
          {['IDENTITY', 'IMPACT', 'ACADEMIA', 'STACK', 'DESIGN'].map((step, i) => (
            <div key={step} className="flex flex-col items-center gap-2 group cursor-pointer">
              <div className={`w-14 h-14 rounded-2xl flex items-center justify-center border-2 transition-all 
                ${i === 0 ? 'bg-indigo-600 border-indigo-600 shadow-lg shadow-indigo-100' : 'bg-white border-transparent text-slate-300 group-hover:border-slate-200'}`}>
                <div className={`w-2 h-2 rounded-full ${i === 0 ? 'bg-white' : 'bg-current'}`} />
              </div>
              <span className={`text-[9px] font-black tracking-tighter ${i === 0 ? 'text-indigo-600' : 'text-slate-400'}`}>{step}</span>
            </div>
          ))}
        </div>

        {/* ‰∏≠Èó¥Ê†∏ÂøÉË°®ÂçïÔºöIdentity Matrix */}
        <div className="flex-1 space-y-8">
          <div className="space-y-2">
            <h1 className="text-4xl font-black text-slate-900 tracking-tight">Identity Matrix</h1>
            <p className="text-slate-400 font-medium">Refine your core contact information for global recruitment standard.</p>
          </div>

          <div className="bg-white p-12 rounded-[40px] border border-slate-100 shadow-sm space-y-10">
            <div className="grid grid-cols-2 gap-10">
              <div className="space-y-3">
                <label className="text-[10px] font-black text-slate-400 uppercase tracking-[0.2em]">Given Name</label>
                <Input defaultValue="Alex" className="h-14 bg-slate-50 border-none rounded-2xl font-bold text-lg focus:ring-2 ring-indigo-500/20" />
              </div>
              <div className="space-y-3">
                <label className="text-[10px] font-black text-slate-400 uppercase tracking-[0.2em]">Family Name</label>
                <Input defaultValue="Chen" className="h-14 bg-slate-50 border-none rounded-2xl font-bold text-lg focus:ring-2 ring-indigo-500/20" />
              </div>
              <div className="space-y-3">
                <label className="text-[10px] font-black text-slate-400 uppercase tracking-[0.2em]">Professional Email</label>
                <Input defaultValue="alex.chen@university.edu" className="h-14 bg-slate-50 border-none rounded-2xl font-bold text-lg" />
              </div>
              <div className="space-y-3">
                <label className="text-[10px] font-black text-slate-400 uppercase tracking-[0.2em]">Current Base (City, ST)</label>
                <Input defaultValue="Seattle, WA" className="h-14 bg-slate-50 border-none rounded-2xl font-bold text-lg" />
              </div>
            </div>

            <button className="w-full h-20 bg-slate-900 text-white rounded-3xl font-black uppercase tracking-[0.2em] flex items-center justify-center gap-4 hover:bg-black transition-all">
              <span>Export Master CV</span>
              <div className="h-8 w-[1px] bg-white/20" />
              <div className="flex items-center gap-2">
                <span className="text-indigo-400 italic">80% Matched</span>
                <div className="w-8 h-8 rounded-full border-2 border-indigo-500 flex items-center justify-center text-[10px]">80</div>
              </div>
            </button>
          </div>
        </div>

        {/* Âè≥‰æßÔºöFAANG ÂçèËÆÆÂÆ°ËÆ°Èù¢Êùø */}
        <aside className="w-80 flex flex-col gap-6">
          <div className="bg-white p-8 rounded-[32px] border border-slate-100 shadow-sm">
            <div className="flex justify-between items-center mb-10">
              <h3 className="text-[11px] font-black text-slate-400 uppercase tracking-[0.2em]">FAANG Protocol Audit</h3>
              <ShieldCheck className="text-indigo-600 w-5 h-5" />
            </div>
            
            <div className="space-y-8">
              <AuditItem label="Auth Email Format" status="VERIFIED" isDone={auditStatus.email} />
              <AuditItem label="US Location Syntax" status="VERIFIED" isDone={auditStatus.location} />
              <AuditItem label="Quantified Impacts (3+)" status="ACTION REQUIRED" isDone={auditStatus.impacts} urgent />
              <AuditItem label="Action Verb Priority" status="VERIFIED" isDone={auditStatus.verbs} />
            </div>
          </div>

          {/* A4 ÂÆûÊó∂È¢ÑËßàÁº©Áï•Âõæ */}
          <div className="flex-1 bg-white rounded-[32px] border border-slate-100 p-4 relative group cursor-zoom-in">
            <div className="w-full h-full bg-slate-50 rounded-2xl overflow-hidden grayscale opacity-40 group-hover:opacity-100 transition-all">
               {/* ËøôÈáåÂèØ‰ª•ÊîæÈ¢ÑËßàÁöÑÂ∞èÁªÑ‰ª∂ */}
               <div className="p-4 space-y-2">
                 <div className="h-4 w-1/2 bg-slate-200 rounded" />
                 <div className="h-2 w-full bg-slate-200 rounded" />
                 <div className="h-2 w-full bg-slate-200 rounded" />
               </div>
            </div>
            <div className="absolute inset-0 flex items-center justify-center opacity-0 group-hover:opacity-100 transition-all">
              <span className="bg-black text-white text-[10px] font-black px-4 py-2 rounded-full uppercase tracking-widest">Live Preview</span>
            </div>
          </div>
        </aside>
      </main>
    </div>
  );
}

function AuditItem({ label, status, isDone, urgent }: any) {
  return (
    <div className="flex items-center gap-4">
      <div className={`w-9 h-9 rounded-full flex items-center justify-center transition-all ${isDone ? 'bg-emerald-100 text-emerald-600' : 'bg-slate-50 text-slate-200'}`}>
        {isDone ? <CheckCircle2 size={18} /> : <Circle size={18} strokeWidth={3} />}
      </div>
      <div className="flex flex-col">
        <span className="text-[11px] font-black text-slate-800 uppercase tracking-tight leading-none mb-1">{label}</span>
        <span className={`text-[9px] font-black uppercase tracking-widest ${urgent && !isDone ? 'text-indigo-600 italic' : 'text-slate-300'}`}>
          {status}
        </span>
      </div>
    </div>
  );
}